import * as FA from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import L from 'leaflet'
import 'leaflet-imageoverlay-rotated'
import 'leaflet-toolbar'
import React, { useEffect, useRef, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { Button, Input, Modal } from 'rsuite'
// import { getDemoGeoJSON } from '../http/API'
import MapControls from '../map/MapControls'
import '../points.json'
import '../styles/sidenav.css'

const MapRS = () => {
  const [jsonToDisplay, setJSON] = useState([])
  const [openReg, setOpenReg] = useState(false)
  const [openLogin, setOpenLogin] = useState(false)

  const [mapType, setMap] = useState('https://tile.openstreetmap.org/{z}/{x}/{y}.png')
  function sortByProperty (property) {
    return function (a, b) {
      if (a[property] > b[property]) {
        return 1
      } else if (a[property] < b[property]) {
        return -1
      }
      return 0
    }
  }

  const { t, i18n } = useTranslation()

  const [isLoading, setIsLoading] = useState(false)

  const mapRef = useRef(null)
  const paneRef = useRef(null)
  const drawnLayerRef = useRef(null)
  const markerRef = useRef(null)
  const showLayerRef = useRef(null)
  const tileLayerRef = useRef(null)
  const heatmapLayer = useRef(null)
  const searchGeometryRef = useRef(null)

  let map

  function clearLayers () {
    if (mapRef?.current) {
      // if (mapRef.current.hasLayer(searchGeometryRef.current)) searchGeometryRef.current.clearLayers()
      if (mapRef.current.hasLayer(drawnLayerRef.current)) drawnLayerRef.current.clearLayers()
      // if (mapRef.current.hasLayer(markerRef.current)) markerRef.current.clearLayers()
      if (mapRef.current.hasLayer(tileLayerRef.current)) tileLayerRef.current.clearLayers()
      // if (mapRef.current.hasLayer(heatmapLayer.current)) heatmapLayer.current.clearLayers()
    }
  }

  /** map initialization */
  useEffect(() => {
    try {
      map = L.map('map', {
        center: [49.237257, 69.9804824],
        zoom: 5,
        minZoom: 1,
        zoomControl: false,
        attributionControl: false
      })
      L.tileLayer(mapType, {
        subdomains: ['mt0', 'mt1', 'mt2', 'mt3'],
        maxZoom: 18,
        id: 'baselayer'
      }).addTo(map)
      L.control.attribution({
        position: 'bottomright'
      }).addTo(map)
      // map.fitBounds([[47.12995075666307, 52.064208984375], [51.1807, 71.461]])

      // Searched geometries
      searchGeometryRef.current = L.featureGroup().addTo(map)
      showLayerRef.current = L.featureGroup().addTo(map)
      drawnLayerRef.current = L.featureGroup().addTo(map)
      tileLayerRef.current = L.featureGroup().addTo(map)
      // heatmapLayer.current = HeatmapLayer()
      // heatmapLayer.current.addTo(map)

      mapRef.current = map
      // Pane for tiles topping on the map
      const topPane = mapRef?.current.createPane('leaflet-top-pane', mapRef.current.getPanes().mapPane)
      paneRef.current = topPane

      // Adding Controls
      MapControls(mapRef.current, t)
      mapRef.current.invalidateSize(9)
    } catch (e) {

    }
  }, [])
  useEffect(() => {
    // mapRef.current.removeLayer('baselayer')
    mapRef.current.attributionControl.setPrefix('')
    L.tileLayer(mapType, {
      maxZoom: 18,
      id: 'baselayer'
    }).addTo(mapRef.current)
  }, [mapType])
  useEffect(() => {
    searchGeometryRef.current.clearLayers()
    fetch('../points.json').then(async (response) => {
      const json = await response.json()
      console.log(json)
      json.features.forEach((f) => {
        const mask = L.geoJSON(f)
        const name = f.properties?.Name

        mask.addEventListener('mouseover', () => {
          mask.unbindTooltip()
          mask.bindTooltip(() => name)
        })
        mask.addEventListener('mouseout', () => {
          mask.unbindTooltip()
          // eslint-disable-next-line no-sequences
          mask.bindTooltip(() => (name, { permanent: true }))
        })

        mask.addEventListener('mouseover', () => (mask.openTooltip()))
        searchGeometryRef.current.addLayer(mask)
      })
    })
  }, [])

  const fitBounds = (field) => {
    const mask = L.geoJSON(field)

    if (field.geometry?.type === 'Point') {
      const lat = field.geometry.coordinates[1]
      const lng = field.geometry.coordinates[0]
      mapRef.current.panTo([lat, lng], 10)
    } else {
      mapRef.current.fitBounds(mask.getLayers()[0].getLatLngs())
    }
  }

  return (
    <div
      data-theme="main"
      className="lux-body no-touch full"
      style={{ cursor: 'auto' }}
    >
      <header
        className="navbar navbar-default navbar-fixed-top"
        role="navigation"
      >
        <div className="container-fluid">
          <div className="navbar-inner">
            <div className="pull-left brand">
              <p>
                <img src="logo1.png" alt="" height='50px' />
                <img src="logo2.png" alt="" height='50px' />
                <img src="logo3.png" alt="" height='50px' />
                -        
                </p>
            </div>
          </div>

          <ul className="nav navbar-nav pull-right">
            <li className="visible-xs-inline-block search icon">
              <a>Search</a>
            </li>

            <li className="user icon" onClick={() => setOpenLogin(true)}>
              <a className="ng-pristine ng-untouched ng-valid ng-not-empty">
                User
              </a>
            </li>
            <li className="lang icon">
              <a className="ng-pristine ng-untouched ng-valid ng-not-empty">
                Language
              </a>
            </li>
          </ul>

          <div className="ng-isolate-scope">
            <div className="search-container">
              <form className="navbar-form ng-pristine ng-valid" role="search">
                <div className="form-group">
                  <span
                    className="twitter-typeahead"
                    style={{ position: 'relative', display: 'inline-block' }}
                  >
                    <input
                      type="text"
                      autoCorrect="off"
                      autoCapitalize="none"
                      className="form-control tt-input"
                      placeholder=" ... "
                      spellCheck="false"
                      dir="auto"
                      aria-activedescendant=""
                      aria-owns="undefined_listbox"
                      role="combobox"
                      aria-readonly="true"
                      aria-autocomplete="list"
                      style=
                      {{
                        position: 'relative',
                        verticalAlign: 'top',
                        backgroundColor: 'transparent'
                      }}

                    />
                  </span>
                  <span className="clear-button"></span>
                <button className="btn btn-link facet-toggle">
                  <FontAwesomeIcon icon={FA.faSearch} color='white'/>
                </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </header>
      <div id="main-container">
        <div id="sidebar" className="open">
          <div id="layers" className="show">
            <h2></h2>
            <div className="tab-content">
              <div id="catalog" className="tab-pane active">
                <div className="ng-isolate-scope">
                  {/* <div className="theme-selector" href=".themes-switcher">
                    <span>Theme</span>: <span>Main</span>
                    <a>
                      <span>Change</span>
                      <table>
                        <tbody>
                          <tr>
                            <td style={{ backgroundColor: '#2980b9' }}></td>
                            <td style={{ backgroundColor: '#f39c12' }}></td>
                            <td style={{ backgroundColor: '#e74c3c' }}></td>
                            <td style={{ backgroundColor: '#03a2ac' }}></td>
                          </tr>
                          <tr>
                            <td style={{ backgroundColor: '#879c31' }}></td>
                            <td style={{ backgroundColor: '#51976e' }}></td>
                            <td style={{ backgroundColor: '#be8541' }}></td>
                            <td style={{ backgroundColor: '#d10074' }}></td>
                          </tr>
                        </tbody>
                      </table>
                    </a>
                  </div> */}
                  <div
                    id="themes-content"
                    className="themes-switcher collapse in"
                    aria-expanded="true"
                  >
                    <ul className="themes-list clearfix">
                      <li className="theme-main">
                        <a style={{ fontSize: '25px' }} href = '/db'> </a>
                      </li>

                      <li className="theme-tourisme">
                        <a style={{ fontSize: '15px' }} href = '/scibase'>- </a>
                      </li>

                      <li className="theme-emwelt">
                        <a style={{ fontSize: '20px' }} href = '/field'> </a>
                      </li>

                      <li className="theme-eau">
                        <a style={{ fontSize: '25px' }} href = 'https://rsgiskaznu.maps.arcgis.com/apps/instant/sidebar/index.html?appid=bb853afacaa34044830069057bc7c570' target='_blank' rel="noreferrer"> </a>
                      </li>

                      <li className="theme-pag">
                        <a style={{ fontSize: '15px' }} href = '/maps'></a>
                      </li>

                      <li className="theme-agriculture">
                        <a style={{ fontSize: '20px' }} href = '/recs'>  </a>
                      </li>

                      <li className="theme-lenoz">
                        <a style={{ fontSize: '25px' }} href = '/eval'></a>
                      </li>

                      <li className="theme-at">
                        <a style={{ fontSize: '25px' }} href = '/sem'>  </a>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="map-wrapper">
          <app-map className="ng-isolate-scope">
            <div
              className="app-map mapboxgl-map blankBackground"
              style={{ cursor: 'default' }}
            >
              <div
                className="ol-viewport"
                style=
                {{
                  position: 'relative',
                  overflow: 'hidden',
                  width: '100%',
                  height: '100%'
                }}
                id='map'
              >
              </div>
            </div>
          </app-map>
        </div>
      </div>
      <footer className="footer">
        <ul className="sidebar-controls nav navbar-nav pull-left open">
          <li className="layers icon ng-pristine ng-untouched ng-valid ng-not-empty">
            <a></a>
          </li>
          <li className="mymaps icon ng-pristine ng-untouched ng-valid ng-not-empty">
            <a>
              <span className="ng-scope ng-hide"></span>
            </a>
          </li>
          <li className="infos icon ng-pristine ng-untouched ng-valid ng-not-empty">
            <a>
              <span className="ng-scope ng-hide"></span>
            </a>
          </li>
          <li className="legends icon ng-pristine ng-untouched ng-valid ng-not-empty">
            <a></a>
          </li>
          <li className="routing icon ng-pristine ng-untouched ng-valid ng-not-empty">
            <a></a>
          </li>
        </ul>
        <ul className="footer-navigation nav navbar-nav pull-right hidden-xs">
          <li className="hidden-sm">
            <a
              target="_blank"
              href=""
              rel="noreferrer"
            >
              
            </a>
          </li>
          <li className="hidden-sm">
            <a
              target="_blank"
              href=""
              rel="noreferrer"
            >
              
            </a>
          </li>

          <li className="hidden-sm">
            <a> </a>
          </li>
          <li className="hidden-sm">
            <a
              target="_blank"
              href=""
              rel="noreferrer"
            >
               
            </a>
          </li>
          <li className="hidden-sm">
            <a
              target="_blank"
              href=""
              rel="noreferrer"
            >
              
            </a>
          </li>
          <li className="hidden-sm">
            <a
              target="_blank"
              href=""
              rel="noreferrer"
            >
              
            </a>
          </li>
          <li>
            <a
              target="_blank"
              href=""
              rel="noreferrer"
            >
               
            </a>
          </li>
        </ul>
        <ul className="toolbox nav navbar-nav">
          <li className="draw icon hidden-3d">
            <a className="ng-pristine ng-untouched ng-valid ng-not-empty">
              
            </a>
          </li>
          <li className="hidden-xs measure icon hidden-3d">
            <a className="ng-pristine ng-untouched ng-valid ng-not-empty">
              
            </a>
          </li>
          <li className="hidden-xs hidden-3d print icon">
            <a className="ng-pristine ng-untouched ng-valid ng-not-empty">
              
            </a>
            <app-print className="ng-isolate-scope"> </app-print>
          </li>
          <li className="share icon">
            <a className="ng-pristine ng-untouched ng-valid ng-not-empty">
              
            </a>
          </li>
        </ul>
      </footer>
      <Modal show={openLogin} onHide={() => setOpenLogin(false)} backdrop='static' size='lg'>
        <Modal.Header>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <h5>   :</h5>
          <br />
          <Input placeholder=' *' />
          <br />
          <Input placeholder=' *' />
          <br />
          <Button appearance='primary'></Button>
          <br />
          <br />
          <a className='pointer' onClick={() => { setOpenLogin(false); setOpenReg(true) }}>  </a>
        </Modal.Body>
      </Modal>
      <Modal show={openReg} onHide={() => setOpenReg(false)} backdrop='static' size='lg'>
        <Modal.Header>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <h5>  </h5>
          <br />
          <Input placeholder=' *' />
          <br />
          <Input placeholder=' *' />
          <br />
          <Input placeholder='Email *' />
          <br />
          <Input placeholder=' *' />
          <br />
          <Input placeholder=' *' />
          <br />
          <Button appearance='primary'></Button>
          <br />
          <br />
          <a className='pointer' onClick={() => { setOpenLogin(true); setOpenReg(false) }}>   </a>
        </Modal.Body>
      </Modal>
    </div>
  )
}

export default MapRS
