import React from 'react'
import { Route, BrowserRouter as Router, Switch } from 'react-router-dom'
import MapDemo from './map/MapBasic'
import SciBase from './pages/SciBase'
import MapRS from './pages/MapRS'
import MapNew from './map/MapNew'

function App () {
  return (
    <>
      <Router>
        <Switch>
          {/* <Route path='/auth' component={Auth} /> */}
          {/* <Route path='/profile' component={Profile} /> */}
          <Route path='/scibase' component={SciBase} />
          <Route path='/field' component={MapRS} />
          <Route path='/lux' component={MapNew} />
          {/* <Route path='/admin' component={userRole === 'ADMIN' ? AdminSettings : Profile} /> */}
          <Route exact path='*' component={MapNew} />
        </Switch>
      </Router>
    </>
  )
}

export default App
