import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import * as API from '../http/API'
import { Alert, Button, Input, Modal } from 'rsuite'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import * as FA from '@fortawesome/free-solid-svg-icons'
import RichTextEditor from 'react-rte'

const SciBase = () => {
  const { t, i18n } = useTranslation()

  const [ukey, update] = useState(0)
  const [newsList, setNews] = useState()
  const [dataPub, setDataPub] = useState({ contentRTF: RichTextEditor.createEmptyValue() })
  const [open, setOpen] = useState(false)

  const handleOpen = () => setOpen(true)
  const handleClose = () => setOpen(false)

  const fetchNews = async () => {
    await API.getFeed().then((r) => {
      setNews(r.news)
    }).catch(e => { console.log(e) })
  }
  const publishNews = async () => {
    dataPub.content = dataPub?.contentRTF.toString('html')
    await API.postFeed(dataPub).then((r) => {
      setDataPub({ contentRTF: RichTextEditor.createEmptyValue() })
      handleClose()
      Alert.success(' !')
      update(ukey + 1)
    }).catch(e => { console.log(e) })
  }

  useEffect(() => {
    fetchNews()
  }, [ukey])

  return (
    <div>
      <a href='/'><Button appearance='primary'><FontAwesomeIcon icon={FA.faHome} /></Button></a><h1 className='title-h1'>- </h1>
      <hr />
      <div className='news-wrap'>
        <Button onClick={handleOpen}><h5>+ </h5></Button>
        <br />
        {newsList?.map(n =>
          <div key={n?.title} className='news-item'>
            <h4>{n.title}</h4>
            <hr />
            <RichTextEditor readOnly value={RichTextEditor.createValueFromString(n.content, 'html')} />
            {/* <hr />
            <h6>:</h6>
            <ul>
              {n.attachments.map((attachment, index) => (
                <li key={index}>
                  <a href={attachment.blob} download={attachment.name}>{attachment.name}</a>
                </li>
              ))}
            </ul> */}
          </div>
        )}
      </div>
      <Modal show={open} onHide={handleClose} size='md'>
        <Modal.Header>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Input placeholder='' value={dataPub?.title} onChange={e => setDataPub({ ...dataPub, title: e.slice(0, 80) })} />
          {/* <Input componentClass="textarea" rows={5} placeholder=" ( 1000 )" value={dataPub?.content} onChange={e => setDataPub({ ...dataPub, content: e.slice(0, 1000) })}/> */}
          <RichTextEditor
            value={dataPub?.contentRTF}
            onChange={e => setDataPub({ ...dataPub, contentRTF: e })}
            placeholder=''
          />
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={publishNews} appearance='primary'>
            
          </Button>
          <Button onClick={handleClose} appearance='subtle'>
            
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  )
}

export default SciBase
